import React, {useState, useEffect} from 'react'
import Axios from 'axios';
import NewsItem from './NewsItem';
import './NewsItem.css'
import { covidData } from './covidData';
const NewsList = () => {
    const [articles, setArticles] = useState([]);
   
    useEffect(()=>{
        const getArticles = async()=>{
            // const res = await fetch("https://newsapi.org/v2/everything?q=coronavirus&sortBy=publishedAt&apiKey=793d388cf40243f2a0e2c02d3703fcdb");
            // const res = await fetch("https://newsdata.io/api/1/news?apikey=pub_240431744a7259f2ca5fa5f2ac61ccd898e1&q=covid");
            // const final = await res.json();
            // setArticles(final.articles);
            // setArticles(data.articles);
            // console.log(final);
            setArticles(covidData);
        };
        getArticles(); 
    },[])
    return (
        <div className="total_data">
            {
                articles.map(({title, description, url, urlToImage})=>(
                    <NewsItem title={title} description={description} url={url} urlToImage={urlToImage} />
                ))
            }
        </div>
    )
}

export default NewsList
